.img1{
  /* padding-top: 100px; */
  width: 100%;
  height: 90%;
  padding-right: 10vw;
  padding-top: 30vh;
  padding-left: 5vw;

}
@media only screen and (max-width: 420px){
  .img1{display: none;}
}
@media only screen and (max-width: 915px){
  .img1{ width: 100%;
    height: 60%;
    padding-right: 10vw;
    padding-top: 0;
    padding-left: 5vw;}
}
@media only screen and (max-width: 540px){
  .img1{ width: 100%;
    height: 100%;
    padding-right: 0;
    padding-top: 0;
    padding-left: 0;
  }
}



::-webkit-scrollbar {
  width: 0px;
}

::-webkit-scrollbar-thumb {
  background: rgba(136, 136, 136, 0.281);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.messages {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  overflow-y: scroll;
  scrollbar-width: none;
}

